
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Scroll from "@/components/scrollAll";
import NoData from "../../components/noData";
import Query from "../../components/filter";

export default {
components: {
NoData,
Flexbox,
FlexboxItem,
XHeader,
Scroll,
Datetime,
Group,
XButton,
Query
},
data() {
return {
hideBtn:false,
clientList: [],
params: {
startTime: "",
endTime: "",
pageIndex: 1,
pageSize: 20,
createUserId:this.$store.state.CurrentUserID
},
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
isSort: false,
};
},
methods: {
back(){
if (this.$route.query.backIndex && this.$route.query.backIndex == 1) {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
} else {
this.$router.back();
}
},
clearTime(idx) {
if (idx == 1) {
this.params.startTime = "";
} else {
this.params.endTime = "";
}
},
doSearch(value) {
this.params.startTime=value.beginDate
this.params.endTime=value.endDate
if(value.beginDate && value.endDate){
this.getData(1);
}
this.$refs["scroll"].scrollTo(0, 0, 200);

},
selectedUser(ID){
this.params.createUserId = ID
this.getData(1)
this.$refs["scroll"].scrollTo(0, 0, 200);
},
gotoDetail(item) {
this.$router.push({
path: "/checklist/checkDetail",
query: {
cid: item.ID
}
});
},
gotoMap(){
plus.webview.open("../map/template.html?key=tripMapTotal&userid=" + this.params.createUserId);
},
getData(type) {

let _url = "/Work/GetMyCrmWorkSigninList";

let _params = JSON.parse(JSON.stringify(this.params));
if (this.params.startTime && this.params.endTime) {
if (this.params.startTime > this.params.endTime) {
this.$vux.toast.show({
text:"开始时间大于结束时间,请重新选择",
position: "middle",
type: "cancel"
});
return false;
}
_params.startTime = this.params.startTime;
_params.endTime = this.params.endTime;
} else {
this.params.startTime = "";
this.params.endTime = "";
}
if (type == 1) {
this.params.pageIndex = 1;
_params.pageIndex = 1;
}
this.$vux.loading.show({
text: "加载中"
});
this.Axios.get(_url, { params: _params }).then(res => {
this.$vux.loading.hide()
let _list = res.data.Data.List;
if (_list.length > 0) {
if (type === 2) {
this.clientList = [...this.clientList, ..._list];
this.$refs.scroll.scroll.finishPullUp();
} else {
this.clientList = _list;
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate(true);
}
this.params.pageIndex += 1;
this.$refs.scroll.refresh();
} else {
if(type==1){
this.clientList = []
}
this.$refs.scroll.forceUpdate();
}
});
}
},
mounted() {
if(this.$route.query.hideBtn && this.$route.query.hideBtn==1){
this.hideBtn=true;
}
if (this.$route.query.backIndex && this.$route.query.backIndex == 1){
document.addEventListener("plusready",()=>{
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
});
}
this.getData();
},
watch: {
clientList() {
setTimeout(() => {
this.$refs.scroll.forceUpdate(true);
}, 500);
}
},
filters: {
getPicSrc(id) {
return `${apiPic}/dXNlci9sb2dv_${id}_35x35.png`;
}
}
};



